const baseUrl = "http://192.168.43.70:8088";//url
export function request(options){//requset
	let { url,data,method } = options;
	data = data || {},
	
	uni.showToast({//loading
	    title: '',
	    duration: 200,
		icon:"loading"
	});
	return new Promise((resolve,rejext)=>{
		uni.request({//uniappapi
		    url: baseUrl + url, //
		    data,
			method:method||"GET",//get
		    success: (res) => {
				resolve(res)
		    }
		});
	})
}

export function showToast(options){//Toast
	let { title,duration,icon } = options;
	uni.showToast({
	    title:title,
	    duration: 2000,
		icon:icon || "none"
	});
}

